@import url(https://fonts.googleapis.com/css?family=Titan+One);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #F8F8D9;
  font-size: 17px;
  line-height: 24px;
  font-weight: 400;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body h1 {
  font-family: "Titan One";
  font-size: 48px;
  line-height: 56px;
}

body h2 {
  font-family: "Titan One";
  font-size: 36px;
  line-height: 40px;
}

body h3 {
  font-family: "Titan One";
  font-size: 28px;
  line-height: 32px;
}

body h4 {
  font-family: "Titan One";
  font-size: 24px;
  line-height: 28px;
}

body h6 {
  font-family: "Titan One";
  font-size: 17px;
  line-height: 24px;
}

.app {
    width: 100%;
    max-width: 1200px;
    margin: 0 auto;
}

.loading {
    margin-left: 30%;
}
.logo {
    display: block;
    text-decoration: none;
    margin: 48px 0 24px;
    text-align: center;
    color: #301934;
}

.button {
  font-family: 'Titan One';
  cursor: pointer;
  position: relative;
  transition-property: background-color, border, box-shadow;
  transition-duration: 0.25s;
  transition-timing-function: ease;
  color: #301934;
  border: 1px solid #301934;
  background-color: white;
  border-radius: 4px;
  max-width: 180px;
  max-height: 70px;
}

.button:hover {
  border-color: #fcc521;
  background-color: #fcc521;
}

.button h4 {
  margin: auto auto auto auto;
}
.search {
    position: relative;
    width: 100%;
}

.search input {
    font-size: 24px;
    line-height: 36px;
    width: 100%;
    background: white;
    color: #333;
    outline: none;
    border: solid #333 1px;
    border-radius: 4px;
    padding: 4px 12px;
    height: 48px
}

.search img {
    background-color: white;
    position: absolute;
    top: 6px;
    right: 12px;
    height: 35px;
    width: 35px;
}

.reg_btn {
    text-align: center;
    margin-right: 40px;
}
.card {
    border-radius: 12px;
    width: 300px;
    height: 240px;
    background-color: rgba(255, 255, 255, 0.24);
    background-size: cover;
    position: relative;
    transition-property: box-shadow;
    transition-duration: 0.2s;
    transition-timing-function: ease;
}

.card:hover {
    background-color: white;
}

.card .like {
    position: absolute;
    top: 20px;
    right: 20px;
}

.card .title {
    color: white;
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    padding: 12px;
    -webkit-backdrop-filter: blur(4px);
            backdrop-filter: blur(4px);
    background: rgba(0,0,0, 0.15);
    box-sizing: border-box;
    border-bottom-left-radius: 12px;
    border-bottom-right-radius: 12px;
}

.external_card {
    position: absolute;
    top: 20px;
    left: 20px;
    z-index: 1;
}

.card_link {
    text-decoration: none;
}

.card:hover {
    box-shadow: 4px 4px 8px rgba(0,0,0, 0.3);
}

.external {
    color: black;
    width: 30px;
    height: 30px;
    font-family: 'Titan One';
}

.external_img {
    width: 100%;
    height: 100%;
    background-color: #ffffff96;
    border-radius: 50%;
    transition-property: background-color;
    transition-duration: 0.25s;
    transition-timing-function: ease;
}

.external_img:hover {
    background-color: #00000036;
}

.external_box {
    background-color: white;    
    width: 120px;
    border: solid 1px black;
    border-radius: 4px;
}

.external_days_title {
    padding: 4px;
    height: 20px;
}

.external_text {
    margin-top: 0px;
}

.close {
    position: relative;
    left: 90px;
    bottom: 38px;
    height: 20px;
    width: 20px;
}

.external_days {
    display: flex;
    align-items: flex-start;
    flex-direction: column;
    height: 183px;
}
.external_day {
    height: 23px;
    width: 116px;
    margin: 1px 1px 0px 1px;
    border: solid 1px black;
    transition-property: background-color, border;
    transition-duration: 0.25s;
    transition-timing-function: ease;
}

.external_day:hover {
    background-color: #CCC;
    border: none;
}

.external_day .eat_day {
    display: none;
}

.external_day p {
    margin: 0 0 0 0;
    padding-left: 4px;
}

.external_day:hover .eat_day {
    display: flex;
    background-color: white;
    align-items: flex-start;
    flex-direction: column;
    height: 79px;
    width: 120px;
    margin-left: 118px;
    margin-top: -79px;
    border: solid 1px black;
    border-radius: 4px;
}
.external_eat {
    border: solid 1px black;
    width: 116px;
    transition-property: background-color, border;
    transition-duration: 0.25s;
    transition-timing-function: ease;
    height: 23px;
    margin: 1px 1px 0px 1px;
}

.external_eat:hover {
    background-color: #CCC;
    border: none;
}
.results {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    width: 100%;
    margin: 48px 0;
    grid-gap: 36px;
    gap: 36px;
}

.barbutton {
    padding: 16px;
    color: #301934;
    box-sizing: border-box;
    transition-property: background-color, border, box-shadow;
    transition-duration: 0.25s;
    transition-timing-function: ease;
    background-color: white;
    cursor: pointer;
}

.barbutton:hover {
    border-color: #fcc521;
    background-color: #fcc521;
}

.barbutton h4 {
    margin: 0;
}

.topbar {
    display: flex;
    justify-content: center;
    margin: 0 auto 48px;
}
.form {
    margin: auto auto;
    display: flex;
    flex-direction: column;
    height: 600px;
    align-items: flex-start;
    width: 610px;
}

input {
    height: 30px;
    margin-bottom: 10px;
    background: white;
    color: black;
    outline: none;
    border: solid black 1px;
    border-radius: 4px;
    width: 400px;
}

.submit {
    width: 400px;
    margin-top: 20px;
}

.or {
    font-family: 'Titan One';
    font-size: 24px;
    margin-left: 78px;
    margin-top: -28px;
    margin-bottom: 0;
}

.logout {
    width: 300px;
    height: 100px;
    margin: auto auto;
}

.button {
    width: 300px;
    height: 100px;
    margin-bottom: 50px;
    margin-left: 40px;
}
.dish {
    width: 1200px;
    margin: auto auto;
}

.dish_image {
    width: 600px;
    height: 300px;
}

.dish_info {
    display: flex;
    flex-direction: row;
}

.dish_steps {
    margin-top: -5px;
    margin-left: 20px;
}

.step {
    font-size: 20px;
    text-align: start;
    margin-bottom: 9px;
}

.add_fav_btn {
    position: relative;
    left: 500px;
    font-family: 'Titan One';
    background-color: white;
    border: solid 1px;
    border-radius: 4px;
    height: 30px;
    cursor: pointer;
    transition-property: box-shadow;
    transition-duration: 0.2s;
    transition-timing-function: ease;
}

.add_fav_btn p {
    position: relative;
    text-align: left;
    bottom: 8px;
}

.add_fav_btn:hover {
    box-shadow: 2px 2px 4px rgba(0,0,0, 0.3);
}

.week_btn {
    position: relative;
    left: 500px;
    font-family: 'Titan One';
    background-color: white;
    border: solid 1px;
    border-radius: 4px;
    height: 30px;
    margin-left: 10px;
    cursor: pointer;
    transition-property: box-shadow;
    transition-duration: 0.2s;
    transition-timing-function: ease;

}

.week_btn:hover {
    box-shadow: 2px 2px 4px rgba(0,0,0, 0.3);
}

.ext_menu {
    font-family: 'Titan One';
    margin-left: 720px;
    margin-top: -30px;
}

.bin {
    width: 30px;
    height: 30px;
}

.dish_title_eat {
    width: 400px;
}

.dish_info_eat {
    display: flex;
    flex-direction: column;
}

.dish_image_eat {
    height: 260px;
    width: 380px;
    padding-left: 10px;
    padding-right: 10px;
}

.dish_steps_eat {
    width: 380px;
    padding: 5px 5px 5px 5px;
}

.del_btn {
    position: relative;
    font-family: 'Titan One';
    background-color: white;
    border: solid 1px;
    border-radius: 4px;
    height: 30px;
    cursor: pointer;
    transition-property: box-shadow;
    transition-duration: 0.2s;
    transition-timing-function: ease;
    width: 144px;
    left: calc(400px/2 - 144px/2);
}

.del_btn p {
    position: relative;
    text-align: left;
    bottom: 8px;
}

.del_btn:hover {
    box-shadow: 2px 2px 4px rgba(0,0,0, 0.3);
}

.eat_time {
    cursor: pointer;
}
.day_eats {
    display: flex;
    justify-content: space-around;
    flex-direction: row;
}

.day_info {
    width: 400px;
}

.weekmenu {
    display: flex;
    text-align: center;
    flex-direction: column;
}

.week_day h1 {
    cursor: pointer;
}
